@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --secondary-color: #202020;
}

.page-container {
  height: 150vh;
}

.page-container {
  display: grid;

  grid-template-areas: "navbar navbar" "sidebar content";
  grid-template-columns: 240px 1fr;
  grid-template-rows: 55px 1fr;
}

.navbar-container {
    position: -webkit-sticky;
    position: sticky;
    top: 0px;
    justify-content: space-between;

    display: flex;
    grid-area: navbar;

    padding: 10px 16px;

    background-color: var(--secondary-color);
    color: white;
}

/* LOGO SECTION */

.navbar-container > .logo-section {
    align-items: center;

    display: flex;

    margin-left: 8px;
}

.navbar-container > .logo-section > .logo {
    margin-right: 5px;
    margin-left: 24px;

    max-width: 28px;
    max-height: 28px;
}

.navbar-container > .logo-section > .logo-text {
    font-family: Roboto;
    font-weight: 500;

    letter-spacing: -1px;
}

.navbar-container > .logo-section > .country {
    transform: translate(2px, -5px);

    font-family: Roboto;
    font-size: 12px;
    color: rgb(180, 180, 180);
}

.navbar-container > .logo-section > .menu:hover,
.navbar-container > .logo-section > .logo:hover,
.navbar-container > .logo-section > .logo-text:hover {
    cursor: pointer;
}

/* SEARCH SECTION */

.navbar-container > .search-section {
    display: flex;

    height: 34px;
}

.navbar-container > .search-section > .searchbar {
    border: 1px solid #323232;
    width: 540px;
    padding: 0px 10px;
    border-top-left-radius: 3px;
    border-bottom-left-radius: 3px;

    background-color: #121212;
    color: white;
}

.navbar-container > .search-section > .searchbar:focus {
    border: 1px solid #26649b;
}

.navbar-container > .search-section > .search-button {
    position: relative;
    align-items: center;

    display: flex;

    border: none;
    border-top-right-radius: 3px;
    border-bottom-right-radius: 3px;
    padding: 4px 20px;

    background-color: #323232;
}

.navbar-container > .search-section > .search-button:hover::before {
    opacity: 1;
}

.navbar-container > .search-section > .search-button:hover {
    cursor: pointer;
}

.navbar-container > .search-section > .search-button:hover > svg {
    color: #909090;
}

.navbar-container > .search-section > .search-button > svg {
    color: #696969;
}

/* ACTION SECTION */

.navbar-container > .action-section {
    align-items: center;

    display: flex;
}

.navbar-container > .action-section > .more:hover,
.navbar-container > .action-section > .apps:hover,
.navbar-container > .action-section > .record:hover {
    cursor: pointer;
}

.navbar-container > .action-section > .more {
    margin-right: 15px;
    margin-left: 20px;
}

.navbar-container > .action-section > .apps {
    margin-left: 20px;
}

.navbar-container > .action-section > .record,
.navbar-container > .action-section > .apps,
.navbar-container > .action-section > .more {
    position: relative;

    border: none;
    padding: 0px;

    background: none;
    color: white;
}
.label-container:hover > .label {
    opacity: 1;
}

.label-container > .label {
    position: absolute;
    top: 0px;

    display: inline-block;

    transition: opacity 100ms linear;

    transform: translate(0px, 50px);

    padding: 10px 7px;
    border-radius: 3px;

    background-color: #696969;
    color: white;
    font-size: 13px;
    
    white-space: nowrap;

    opacity: 0;
    pointer-events: none;
}

.label-container {
    justify-content: center;

    display: inline-flex;
}
.sign-in-button {
    align-items: center;

    display: inline-flex;

    border: 1px solid #3EA6FF;
    border-radius: 3px;
    padding: 5px 10px;

    background: none;
    color: #3EA6FF;

    font-family: Roboto;
    font-size: 16px;
    font-weight: 500;
    letter-spacing: -1px;
}

.sign-in-button:hover {
    cursor: pointer;
}

.sign-in-button > svg {
    margin-right: 5px;
    font-size: 26px;
}
.sidebar-container:hover {
    overflow-y: scroll;
}

.sidebar-container {
    position: fixed;
    top: 55px;

    overflow-y: hidden;

    padding-top: 15px;
    width: 240px;
    height: calc(100vh - 70px);

    background-color: #212121;
    color: white;

    font-size: 15px;
}

.sidebar-container > :not(.divider) {
    max-width: 195px;
}

.sidebar-container .divider {
    margin: 10px 0px;
    border-bottom: solid 1px rgb(50, 50, 50);
}

.sidebar-container .sidebar-section-title {
    padding: 5px 0px;
    padding-left: 30px;
    margin: 0px;

    font-size: 15px;
    font-weight: 500;
    color: rgb(180, 180, 180);
}

.sidebar-container .sign-in {
    padding-left: 30px;
}

.sidebar-container .sign-in button {
    margin-top: 15px;
}

.sidebar-container .link-block {
    display: flex;
    flex-wrap: wrap;

    padding-left: 20px;
}

.sidebar-container .link-block:first-child {
    margin-bottom: 15px;
}

.sidebar-container .link-block a {
    padding-left: 6px;

    color: rgb(180, 180, 180);
    font-size: 13px;
    font-weight: 500;
}

.sidebar-container .copyright {
    padding-left: 30px;
    font-size: 13px;
    color: rgb(120, 120, 120);
}
.sidebar-button {
    align-items: center;

    display: flex;

    border: none;
    padding: 0px;
    width: 100%;
    height: 40px;

    color: white;
    background-color: #212121;
    font-size: 15px;
}

.sidebar-button:hover {
    cursor: pointer;
    background-color: #383838;
}

.sidebar-button svg {
    padding-left: 25px;
    padding-right: 20px;

    color: rgb(150, 150, 150);

    font-size: 24px;
}
.content-container {
    grid-area: content;

    padding: 35px 70px;

    background-color: #181818;
    color: white;
}

.content-container > .content-title {
    margin: 0px;
    margin-bottom: 15px;
    font-weight: 400;
    font-size: 21px;
}

.content-container > .video-grid {
    display: grid;

    grid-template-columns: auto auto auto auto;
    grid-row-gap: 25px;
    row-gap: 25px;
}
.video-thumbnail-container > .thumbnail {
    height: 200px;

    object-position: center;
    object-fit: cover;
}

.video-thumbnail-container > .description {
    display: flex;
    margin-top: 5px;
}

.video-thumbnail-container > .description > .channel-icon {
    width: 32px;
    height: 32px;

    border-radius: 50%;
    object-position: center;
    object-fit: cover;
} 

.video-thumbnail-container > .description > .details > .title {
    margin: 0px;

    font-weight: 500;
    color: white;
}

.video-thumbnail-container > .description > .details {
    color: rgb(180, 180, 180);
    font-size: 15px;
    font-weight: 400;
    margin-left: 15px;
}
